"use strict"

document.addEventListener("DOMContentLoaded", ()=>{
  const RATING_FORM = document.getElementById("ratingForm");
  const MESSAGE_ERROR = document.getElementById("messageError");
  const THANK_YOU_SECTION = document.getElementById("thankYouCard");
  
  RATING_FORM.addEventListener("submit", (event)=>{
    // Cancel the submit
    event.preventDefault();
    // Take the data
    const FORM_DATA = new FormData(RATING_FORM);
    // Create an object with the data
    const DATA = {
      "punctuation": FORM_DATA.get("value") 
    }

    // Check weather the user picked a rating
    if(DATA.punctuation === null){
      MESSAGE_ERROR.classList.remove("inactive");
    }else{
      const USER_RATE = document.getElementById("userRate");
      USER_RATE.innerText = DATA.punctuation;
      DATA.punctuation;
      MESSAGE_ERROR.classList.add("inactive");
      RATING_FORM.classList.add("inactive");
      THANK_YOU_SECTION.classList.remove("inactive");
    }
  });
});